export default function(router) {
    router.map({
        /**/
        '/': {
            name: 'home',
            component: function(resolve) {
                require(['./views/index.vue'], resolve);
            }
        },
        /* 404  */
        '*': {
            component: function(resolve) {
                require(['./views/index.vue'], resolve);
            }
        },
        '/userinfo/':{
            name:'userinfo',
            component:function(resolve){
                require(['./views/userinfo.vue'],resolve);
            }
        },
        '/modify/username':{
            component:function(resolve){
                require(['./views/modify/username.vue'],resolve);
            }
        },
        '/modify/phone':{
            component:function(resolve){
                require(['./views/modify/phone.vue'],resolve);
            }
        },
        '/modify/dob':{
            component:function(resolve){
                require(['./views/modify/dob.vue'],resolve);
            }
        },
        '/modify/sex':{
            component:function(resolve){
                require(['./views/modify/sex.vue'],resolve);
            }
        },
        '/modify/add':{
            component:function(resolve){
                require(['./views/modify/add.vue'],resolve);
            }
        },
        '/membership/': {
            name: 'membership',
            component: function(resolve) {
                require(['./views/membership.vue'], resolve);
            }
        },
        '/membership/details/:name': {
            name: 'details',
            component: function(resolve) {
                require(['./views/details.vue'], resolve);
            }
        },
        // '/membership/submitinfo/:name': {
        //     name: 'info',
        //     component: function(resolve) {
        //         require(['./views/submitinfo.vue'], resolve);
        //     }
        // },
        '/membership/success':{
            name: 'successpay',
            component: function(resolve) {
                require(['./views/successplay.vue'], resolve);
            }
        }
    })
}



